import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import { Link } from 'gatsby';
import { breakpoints, colors, textSizes, space } from 'utils/variables';
import { Edge, PALHeaderMenuItem } from 'interfaces/nodes';
import { Heading, Box } from 'components/foundations';
import { isActive } from 'utils/helpers';

const NavMenu = styled('nav')`
  display: flex;
  flex-direction: row;
  position: absolute;
  right: 20px;
  a {
    // padding: 1px 0;
    // color: ${colors.grey07};
    // font-size: ${textSizes[300].fontSize}px;
    // line-height: ${textSizes[300].lineHeight}px;
    // font-weight: 600;

    &:hover,
    &:focus,
    &.active {
      color: ${colors.blue07};
      text-decoration: none;
      outline: none;
    }
  }
`;
interface NavigationMenuProps {
  node: MenuNode;
  menuKey: string;
}
interface ToggleableProps {
  isOpen?: boolean;
}

const ToggleMenu = styled('ul')<ToggleableProps>`
  list-style-type: none;
  margin: 0 -${space.xs}px;
  padding: 0;
  transition: all 0.3s ease;
`;

const ToggleMenuList = styled('li')`
  margin: 0;
  // font-size: 85%;
  color: ${colors.grey07};

  a {
    display: block;
    padding: ${space.xs}px;
    border: 2px solid transparent;
    border-radius: 2px;
    color: ${colors.grey07};

    &:hover,
    &:focus {
      background-color: ${colors.grey02};
      color: ${colors.grey07};
      text-decoration: none;
    }

    &:focus {
      outline: none;
      background-color: ${colors.blue01};
      border-color: ${colors.blue05};
    }

    &.active {
      color: ${colors.grey07};
      background-color: ${colors.blue01};
      font-weight: 500;
      border-color: transparent;
    }
  }
`;

interface MenuProps {
  headerMenus?: Edge<PALHeaderMenuItem>[];
  isDesktop?: boolean;
}

function Menu({ headerMenus, isDesktop }: MenuProps) {
  // const [isDesktop, setIsDesktop] = useState(MenuProps.isDesktop);
  // const { state, dispatch } = React.useContext(NavigationContext);

  function handleMouseOver(e: any) {
    if (
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.id === 'products' ||
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.id === 'consulting'
    ) {
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.className =
        'parentMenuSelected';
    } else {
      e.target.parentElement.parentElement.parentElement.firstElementChild.className = 'parentMenuSelected';
    }
  }

  function handleMouseOut(e: any) {
    if (
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.id === 'products' ||
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.id === 'consulting'
    ) {
      e.target.parentElement.parentElement.parentElement.parentElement.firstElementChild.className = 'link';
    } else {
      e.target.parentElement.parentElement.parentElement.firstElementChild.className = 'link';
    }
  }

  function CheckTheNodeIsMegaMenus(palNode: PALHeaderMenuItem) {
    let isMegaMenus = false;
    if (palNode.palNode.haveChildren && palNode.palNode.childrenNode.length > 1) {
      palNode.palNode.childrenNode.map((n) => {
        if (n.haveChildren && n.childrenNode.length > 1) {
          isMegaMenus = true;
        }
      });
    }
    return isMegaMenus;
  }

  function MenuForDesktop({ headerMenus, isDesktop }) {
    return (
      <NavMenu>
        <div className="palnavbar">
          {headerMenus.map((n) => {
            return <GetParentNode palNode={n.node} key={n.node.id} />;
          })}
        </div>
      </NavMenu>
    );
  }

  function MenuForMobile({ headerMenus, isDesktop }) {
    return (
      <Box mb="xl">
        <ToggleMenu className="toogleMenu">
          {headerMenus.map((n) => {
            return <GetParentNode palNode={n.node} key={n.node.id} />;
          })}
        </ToggleMenu>
      </Box>
    );
  }

  function GetParentNode(palNode: PALHeaderMenuItem) {
    return isDesktop ? (
      palNode.palNode.haveChildren ? (
        <div className="paldropdown">
          <>
            <Link to={palNode.palNode.href} className="link" id={palNode.palNode.id} key={palNode.palNode.id}>
              {palNode.palNode.label}
            </Link>
            <GetChildNode palNode={palNode.palNode} key={'Desktop_' + palNode.palNode.label} />
          </>
        </div>
      ) : (
        <Link to={palNode.palNode.href} className="link" id={palNode.palNode.id} key={palNode.palNode.id}>
          {palNode.palNode.label}
        </Link>
      )
    ) : palNode.palNode.haveChildren ? (
      <ToggleMenuList>
        <Heading as="h3" size={100} color="grey04" mb="sm">
          {palNode.palNode.label}
        </Heading>
        <ToggleMenu className="mega-dropdown">
          <ToggleMenuList className="row">
            <GetChildNode palNode={palNode.palNode} />
          </ToggleMenuList>
        </ToggleMenu>
      </ToggleMenuList>
    ) : (
      <ToggleMenuList key={palNode.palNode.id}>
        <Link to={palNode.palNode.href} id={palNode.palNode.id} key={palNode.palNode.id}>
          {palNode.palNode.label}
        </Link>
      </ToggleMenuList>
    );
  }

  function GetChildNode(palNode: PALHeaderMenuItem) {
    const isMegaMenus = CheckTheNodeIsMegaMenus(palNode);

    return isDesktop ? (
      palNode.palNode.haveChildren === true ? (
        <div className={`${isMegaMenus ? 'palProductMenu' : 'palDD'}`} style={{ right: '0' }}>
          {/*style={`${palNode.palNode.id === 'Resources'} ? {{ right: '0' }} : '{{}}'` */}
          <div className="palrow">
            {palNode.palNode.childrenNode.map((a) => {
              return <GetSubChildNode palNode={a} isItMegaMenu={isMegaMenus} key={'Desktop_' + a.label + '__'} />;
            })}
          </div>
        </div>
      ) : (
        <div className="palDD">
          <div className="palrow">
            <div className="palDropDownColumn">
              <GetSubChildNode
                palNode={palNode.palNode}
                isItMegaMenu={isMegaMenus}
                key={'Desktop_' + palNode.palNode.label}
              />
            </div>
          </div>
        </div>
      )
    ) : (
      <ToggleMenu className="mega-col">
        {palNode.palNode.childrenNode.map((a) => {
          return <GetSubChildNode palNode={a} isItMegaMenu={isMegaMenus} key={'Mobile_Parent_' + a.label} />;
        })}
      </ToggleMenu>
    );
  }

  function GetSubChildNode(palNode: PALHeaderMenuItem, isItMegaMenu: boolean) {
    return isDesktop ? (
      palNode.isItMegaMenu === true ? (
        <div className="palProductColumn" onMouseOut={handleMouseOut} onMouseOver={handleMouseOver}>
          <p>{palNode.palNode.label}</p>
          {palNode.palNode.childrenNode.map((n) => {
            return (
              <Link to={n.href} className="palColItem" id={n.id} key={'Desktop_' + n.label}>
                {n.label}
              </Link>
            );
          })}
        </div>
      ) : (
        <Link
          to={palNode.palNode.href}
          className="palColItem"
          id={palNode.palNode.id}
          key={palNode.palNode.id}
          onMouseOut={handleMouseOut}
          onMouseOver={handleMouseOver}
        >
          {palNode.palNode.label}
        </Link>
      )
    ) : palNode.isItMegaMenu === true ? (
      <ToggleMenuList className="row">
        <ToggleMenu>
          <Heading as="h3" size={100} color="grey04" mb="sm">
            {palNode.palNode.label}
          </Heading>
          {palNode.palNode.childrenNode.map((n) => {
            return (
              <ToggleMenuList key={'Mobile_' + n.label + '__'}>
                <Link to={n.href} id={n.id} key={'Mobile_' + n.label}>
                  {n.label}
                </Link>
              </ToggleMenuList>
            );
          })}
        </ToggleMenu>
      </ToggleMenuList>
    ) : (
      <ToggleMenuList className="row-single">
        <Link to={palNode.palNode.href} id={palNode.palNode.id} key={palNode.palNode.id}>
          {palNode.palNode.label}
        </Link>
      </ToggleMenuList>
    );
  }

  return isDesktop ? MenuForDesktop({ headerMenus, isDesktop }) : MenuForMobile({ headerMenus, isDesktop });
  // return MenuForDesktop({ headerMenus, isDesktop });
  //https://www.codeply.com/go/q0b6ILuRyx/bootstrap-4-vertical-sidebar-nav-with-submenu
}

export default Menu;
